<template>
  <div class="content-title">
    <p class="row-1">
      <i class="sprites icon-rect"></i>
      <span class="title-name">{{title}}</span>
    </p>
    <p class="row-2">{{tips}}</p>
    <slot></slot>
  </div>
</template>
<script>
  export default {
    name: '',
    props: {
      title: {
        default: '',
        type: String
      },
      tips: {
        default: '',
        type: String
      }
    },
    data () {
      return {}
    },
    computed: {},
    methods: {},
    components: {},
    beforeCreate () {},
    created () {},
    beforeMount () {},
    mounted () {},
    beforeUpdate () {},
    updated () {},
    beforeDestroy () {},
    destroyed () {}
  }
</script>
<style lang="scss" rel="stylesheet/scss" scoped>
  @import "../../styles/sprites/sprites.css";

  .content-title {
    padding: 16px 20px;
    background-color: #fff;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
    margin-bottom: 20px;
    .row-1 {
      .title-name {
        position: relative;
        top: -6px;
        left: 10px;
        font-weight: bold;
        font-size: 18px;
      }
    }
    .row-2 {
      padding-left: 20px;
      font-size: 12px;
      color: #66A6E7;
    }
  }
</style>



