            {min: 2, max: 20, message: '长度在2-20之间', trigger: 'blur'}
          ],
          phone: [
            {required: true, message: '请输入手机号', trigger: 'blur'},
            {min: 11, max: 11, message: '手机号的长度为11位', trigger: 'blur'}
          ],
          email: [
            {required: true, message: '请输入账户邮箱', trigger: 'blur'},
            {min: 2, max: 20, message: '长度在2-20之间', trigger: 'blur'}
          ],
          password: [
//            {required: true, message: '请输入登录密码', trigger: 'blur'},
            {min: 6, max: 20, message: '密码长度在6-20之间', trigger: 'change'}
          ],
          'job.id': [
            {required: true, message: '请选岗位', trigger: 'change'},
          ]
        },
        addFormRules: { // 新增部门校验规则
          headBase64: [
            {required: true, message: '请上传头像', trigger: 'blur'},
          ],
          name: [
            {required: true, message: '请输入姓名', trigger: 'blur'},
            {min: 2, max: 20, message: '长度在2-20之间', trigger: 'blur'}
          ],
          phone: [
            {required: true, message: '请输入手机号', trigger: 'blur'},
            {min: 11, max: 11, message: '手机号的长度为11位', trigger: 'blur'}
          ],
          email: [
            {required: true, message: '请输入账户邮箱', trigger: 'blur'},
            {min: 2, max: 20, message: '长度在2-20之间', trigger: 'blur'}
          ],
          password: [
            {required: true, message: '请输入登录密码', trigger: 'blur'},
            {min: 6, max: 20, message: '密码长度在6-20之间', trigger: 'change'}
          ],
          'job.id': [
            {required: true, message: '请选岗位', trigger: 'change'},
          ]
        },
        pageSize: 10, // 每页条数
        pageSizes: [10, 20],
        userDetailDialogVisible: false,
        userEditDialogVisible: false,
      }
    },
    computed: {
      ...mapState([
        'user',
        'userList',
        'totalPages',
        'totalElements',
        'delUserStatus',
        'addUserStatus',
      ]),
      ...mapState('position', [
        'posList'
      ]),
      ...mapGetters([
        'getterUserList'
      ]),
      rolesCpt () {
        return roles
      },
      EmailSuffixCpt () {
        return EmailSuffix
      },
      serverBaseUrlCpt () {
        return serverBaseUrl
      }
    },
    watch: {
      delUserStatus (d) {
        if (d) {
          this.ac_userList({
            pageNo: this.currentPage - 1,
            pageSize: this.pageSize
          }).then(() => {
            this.closeDialog()
            this.$message({
              type: 'success',
              message: '删除成功！'
            })
          })
        }
      },
      addUserStatus (d) {
        if (d) {
          this.ac_userList({
            pageNo: this.currentPage - 1,
            pageSize: this.pageSize
          }).then(() => {
            this.closeDialog()
            this.$message({
              type: 'success',
              message: '操作成功！'
            })
          })
        }
      },
      'formAddEmp.admin' (d) { // 监听选择管理员
        if (d === '1') { // 管理员
          this.formAddEmp.principal = '4'
          this.formAddEmp.reExam = '5'
          this.formAddEmp.job.id = '33' // 管理员岗位
        } else {
          this.formAddEmp.job.id = ''
        }
      }
    },
    methods: {
      ...mapActions('position', [
        'ac_posList',
      ]),
      ...mapActions([
        'ac_userAddAndModify',
        'ac_userList',
        'ac_userDel',
      ]),
      handleDetail (row, column, index) { // 查看
        this.userDetailDialogVisible = true
        this.formAddEmp = {
          headPath: row.headPath,
          headBase64: '',
          name: row.name,
          email: row.email.substr(0, row.email.indexOf('@')),
          password: '',
          phone: row.phone,
          job: {
            id: row.job.id.toString(), // TODO
          },
          principal: row.roles.some((item) => { // 负责人
            return item.id === 4
          }) ? '4' : '0',
          reExam: row.roles.some((item) => {
            return item.id === 5
          }) ? '5' : '0',
          admin: row.roles.some((item) => {
            return item.id === 1
          }) ? '1' : '0'
        }
      },
      handleEdit (row) {
        this.userEditDialogVisible = true
        this.formAddEmp = {
          id: row.id,
          headPath: row.headPath,
          headBase64: '',
          name: row.name,
          email: row.email.substr(0, row.email.indexOf('@')),
          password: '',
          phone: row.phone,
          job: {
            id: row.job.id.toString(), // TODO
          },
          principal: row.roles.some((item) => { // 负责人
            return item.id === 4
          }) ? '4' : '0',
          reExam: row.roles.some((item) => {
            return item.id === 5
          }) ? '5' : '0',
          admin: row.roles.some((item) => {
            return item.id === 1
          }) ? '1' : '0'
        }
      },
      handleEditSave (formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.ac_userAddAndModify(Object.assign({}, {id: this.formAddEmp.id}, this.getAddParam()))
          } else {
            console.log('error submit!!')
            return false
          }
        })
      },
      handleFile2 () { // 前端预览图片
        let f = document.getElementById('editFile').files[0] // 这里file数组，我这里只传一张图片，就取第一个
//        let img = document.getElementById('headImg') // img标签
        let r = new FileReader()
        let that = this // 回调中this的指向问题

        r.readAsDataURL(f)
        r.onload = function () { // this.result 就是图片base64
//          img.src = this.result
          that.formAddEmp.headBase64 = this.result
        }
      },
      handleDel (row) {
        this.$confirm('删除该人员, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.ac_userDel({
            userId: row.id
          })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
      },
      handleSizeChange (size) {
        this.pageSize = size
        this.ac_userList({
          pageNo: this.currentPage - 1,
          pageSize: this.pageSize
        })
      },
      handleCurrentChange (page) {
        this.currentPage = page
        this.ac_userList({
          pageNo: page - 1,
          pageSize: this.pageSize
        })
      },
      addEmployee () {
        this.addEmpDialogVisible = true
      },
      handleFile () { // 前端预览图片
        let f = document.getElementById('addFile').files[0] // 这里file数组，我这里只传一张图片，就取第一个
//        let img = document.getElementById('headImg') // img标签
        let r = new FileReader()
        let that = this // 回调中this的指向问题

        r.readAsDataURL(f)
        r.onload = function () { // this.result 就是图片base64
//          img.src = this.result
          that.formAddEmp.headBase64 = this.result
        }
      },
      addEmpSave (formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.ac_userAddAndModify(this.getAddParam())
          } else {
            console.log('error submit!!')
            return false
          }
        })
      },
      getAddParam (form) {
        let obj = Object.assign({}, this.formAddEmp, {roles: this.getRole()})
        if (obj.headBase64) {
          return {
            job: {id: obj.job.id},
            name: obj.name,
            password: obj.password,
            phone: obj.phone,
            roles: obj.roles,
            email: obj.email + EmailSuffix,
            headBase64: obj.headBase64 // 新增，修改头像
          }
        } else {
          return {
            job: {id: obj.job.id},
            name: obj.name,
            password: obj.password,
            phone: obj.phone,
            roles: obj.roles,
            email: obj.email + EmailSuffix,
            headPath: obj.headPath
          }
        }
      },
      getRole () {
        let role = []
        if (!this.formAddEmp.principal && !this.formAddEmp.reExam && !this.formAddEmp.admin) {
          role.push({id: 6})
          return role
        }
        if (this.formAddEmp.principal === '4') { // 负责人
          role.push({id: this.formAddEmp.principal})
        }
        if (this.formAddEmp.reExam === '5') {
          role.push({id: this.formAddEmp.reExam})
        }
        if (this.formAddEmp.admin === '1') {
          role.push({id: this.formAddEmp.admin})
        }
        return role
      },
      closeDialog () {
        this.addEmpDialogVisible = false
        this.userDetailDialogVisible = false
        this.userEditDialogVisible = false

        this.formAddEmp = {
          headBase64: '',
          headPath: '',
          name: '',
          email: '',
          password: '',
          phone: '',
          job: {
            id: '',
          },
          principal: '0',
          reExam: '0',
          admin: '0'
        }
      }
    },
    components: {
      contentTitle,
      contentC,
      comDialog,
    },
    beforeCreate () {},
    created () {
      this.ac_posList({
        pageNo: 0,
        pageSize: 10000
      })
      this.ac_userList({
        pageNo: this.currentPage - 1,
        pageSize: this.pageSize
      })
    },
    beforeMount () {},
    mounted () {},
    beforeUpdate () {},
    updated () {},
    beforeDestroy () {},
    destroyed () {}
  }
</script>
<style lang="scss" rel="stylesheet/scss" scoped>
  @import "../../../styles/common";

  .add-btn {
    padding: 6px 10px;
    background-color: #45C5C0;
    color: #ffffff;
    width: 110px;
    border-radius: 20px;
    text-align: center;
    float: right;
    margin-right: 10px;
    margin-top: -45px;
    cursor: pointer;
  }

  .t-1 {
    background-color: #45C5C0;
  }

  .x-color-1 {
    color: #45C5C0;
  }

  .head-img {
    width: 80px;
    height: 80px;
    border: 1px solid #eee;
    border-radius: 5px;
    display: inline-block;
  }

  .upload {
    display: inline-block;
    position: relative;
    bottom: 36px;
    width: 110px;
    margin-left: 10px;
    .bt {
      position: absolute;
    }
    .file {
      opacity: 0;
      position: absolute;
      width: 110px;
      height: 36px;
    }
  }

</style>



